import React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
import Hero from "../components/index/hero"

import '../styles/index.css'
import Reasons from "../components/index/reasons"
import VideoSection from "../components/index/video"
import MapSection from "../components/index/map"
import BottomCta from "../components/index/bottom-cta"
import TestimonialSlider from "../components/index/testimonial-slider"
import ImageTextSection from "../components/index/image-text"

import constants from '../constants/index'
import PhoneSection from "../components/index/phone-section"
import HorizontalSteps from "../components/index/horizontal-steps"

const IndexPage = ({ data }) => (
  <Layout>
    <SEO title="Home" />
    
    <Hero />
    <Reasons />
    <VideoSection />
    <MapSection />

    <HorizontalSteps />

    <ImageTextSection
      imgSrc={data.forHomeImage.childImageSharp.fluid}
      imgAlt={'Телевизия и интернет за дома'}
      sectionTitle={<h2>Какво предлагаме<br /><span className='text-purple'> За дома</span></h2>}
      undertext={constants.forHomeSection.text}
      list={constants.forHomeSection.list}
      linkTo={constants.forHomeSection.linkTo}
      linkText={constants.forHomeSection.linkText}
      order={false}
    />

    <ImageTextSection
      imgSrc={data.forOfficeImage.childImageSharp.fluid}
      imgAlt={'Телевизия и интернет за офиса'}
      sectionTitle={<h2>Какво предлагаме<br /><span className='text-purple'> За офиса</span></h2>}
      undertext={constants.forOfficeSection.text}
      list={constants.forOfficeSection.list}
      linkTo={constants.forOfficeSection.linkTo}
      linkText={constants.forOfficeSection.linkText}
      order={true}
    />

    <PhoneSection />
    <TestimonialSlider />
    <BottomCta />
  </Layout>
)

export const query = graphql`
  query IndexPageQuery {
    forHomeImage: file(relativePath: {eq: "index/for-home.png"}) {
      childImageSharp {
        fluid (maxHeight: 700) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    forOfficeImage: file(relativePath: {eq: "index/for-office.png"}) {
      childImageSharp {
        fluid (maxHeight: 700) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`

export default IndexPage
